	}
}

.album {
	padding: 5px;
	box-sizing: border-box;
	transition: all .5s .25s;
}

.album-cover {
	width: 90px;
	transition: all .5s .25s;

	img {
		border: 1px solid transparentize($text-color, .9);
		box-sizing: border-box;
		width: 80px;
		height: 80px;
		border-radius: 50%;
		transition: all .5s .25s;
	}
}

.album-cover-info {
